.nav-container {
  height: 60px;
}
.nav-center {
  display: flex;
  height: 100%;
  justify-content: space-between;
  align-items: center;
  max-width: 1227px;
  margin: 0 auto;
}
.links {
  display: flex;
  justify-content: space-around;
  letter-spacing: var(--spacing);
}

.link-item {
  margin-left: 90px;
  font-size: 14px;
  color: var(--prm-white-2);
}

.link-item:hover {
  color: var(--prm-pink-1);
}

.logo {
  letter-spacing: 0.2rem;
}
.logo-log {
  color: var(--prm-pink-1);
  font-size: 24px;
}
.icon-bars {
  color: var(--prm-pink-1);
  transform: rotate(-90deg);
  font-size: 18px;
}

.btn-toggle {
  background-color: transparent;
  border: none;
  transition: var(--transition);
  cursor: pointer;
  display: none;
}
.btn-toggle:hover {
  transform: rotate(-90deg);
}

@media screen and (max-width: 600px) {
  .nav-container {
    background-color: rgb(0, 0, 0);
  }
  .logo {
    z-index: 999;
  }
  .nav-center {
    background-color: rgb(0, 0, 0);
  }

  .khra {
    display: flex;
    width: 100%;
    margin: 0 2rem;
    justify-content: space-between;
  }

  .icon-bars {
    font-size: 24px;
  }

  .links {
    display: none;
  }

  .link-item {
    margin-top: 25px;
    margin-left: 0;
  }
  .show-links {
    position: absolute;
    z-index: 99;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 150px;
    background-color: black;
    width: 100%;
    flex-direction: column;
    top: 60px;
    animation: toggle 2s;
  }

  @keyframes toggle {
    0% {
      transform: translateY(-100px);
    }
    100% {
      transform: translateY(0px);
    }
  }

  .btn-toggle {
    display: flex;
    z-index: 999;
  }
}
