.contact-container {
  margin-top: 5rem;
  min-height: 100vh;
  text-align: center;
}

.contact-center {
  margin: 0 auto;
  position: relative;
}

.mrtop {
  margin: 3em 0rem;
}

.contact-inner {
  margin-top: 3rem;
  width: 100%;
}

.info-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
}
.info-section p {
  margin-bottom: 2rem;
  color: var(--prm-pink-1);
  opacity: 0.8;
  letter-spacing: var(--spacing);
  font-size: 14px;
  transition: var(--transition);
}
.info-section p:hover {
  color: var(--prm-pink-2);
  -webkit-filter: drop-shadow(0px 0px 4px var(--prm-pink-1));
  -moz-filter: drop-shadow(0px 0px 4px var(--prm-pink-1));
  filter: drop-shadow(0px 0px 4px var(--prm-pink-1));
}

.form {
  display: flex;
  flex-direction: column;
  width: 80%;
  margin: 0 auto;
}

.input {
  margin-bottom: 5rem;
  outline: none;
  color: var(--prm-white-1);
}
.area {
  margin-bottom: 10px;
  outline: none;
  color: var(--prm-white-1);
}

.input,
.area {
  background-color: transparent;
  border: none;
  border-bottom: 1px solid var(--prm-white-1);
}

.area::placeholder {
  color: var(--prm-white-2);
  font-size: 14px;
  opacity: 0.8;
  letter-spacing: var(--spacing);
  font-family: "Staatliches";
}

.input::placeholder {
  color: var(--prm-white-2);
  font-size: 14px;
  opacity: 0.8;
  letter-spacing: var(--spacing);
  font-family: "Staatliches";
}

@media screen and (min-width: 600px) {
  .contact-inner {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    max-width: 1000px;
    padding: 2rem;
    margin: 0 auto;
    margin-top: 3rem;
  }
  .form-section {
    width: 70%;
  }

  .info-section {
    display: flex;
    flex-direction: column;
    text-align: justify;
    width: 20%;
  }
  .mrtop {
    margin: 1em 0rem;
  }
  .info-section p {
    margin-bottom: 5rem;
  }
}
