.skills {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.centered {
  color: var(--prm-white-2);
  letter-spacing: 0.1rem;
  line-height: 1.8rem;
  margin-bottom: 25px;
}

.skills-items {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.bck-skill {
  background: rgba(59, 173, 227, 1);
  background: linear-gradient(
    45deg,
    var(--prm-blue-1) 0%,
    var(--prm-blue-2) 25%,
    var(--prm-blue-3) 51%,
    var(--prm-pink-2) 100%
  );
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.5rem;
  width: 25%;
  height: 100px;
  transition: var(--transition);
}

.p {
  font-size: 18px;
  font-family: "overpass";
  color: var(--prm-white-1);
}

.bck-skill:hover {
  transform: translateY(-10px);
}
.icon-skill {
  font-size: 34px;
  color: var(--prm-white-1);
}

#django {
  font-size: 54px;
}
