.hero-container {
  width: 100%;
  height: 100vh;
  position: relative;
  z-index: 999;
}
.video {
  -o-filter: blur(20px);
  filter: blur(50px);
}

.hero-text-container {
  width: 100%;
  padding: 2rem;
  height: 100%;
  position: relative;
  margin-top: 2.5rem;
}
.fv {
  flex-direction: column;
  -webkit-box-align: center;
  align-items: center;
  position: absolute;
  left: 2rem;
  top: 8rem;
  animation: appear 2s;
  display: none;
}
.gmail-link {
  font-family: "dongle";
  font-size: 20px;
  color: var(--prm-pink-1);
  -webkit-writing-mode: vertical-rl;
  writing-mode: vertical-rl;
  transform: rotate(-180deg);
  transition: var(--transition);
  margin: 15px 0;
}
hr {
  display: block;
  height: 170px;
  margin: 0px auto;
  background-color: var(--prm-pink-1);
}

.icon-fv {
  display: none;
}

.text-section {
  text-align: center;
}

.heading-4 {
  color: var(--prm-blue-4);
  font-size: 16px;
  letter-spacing: var(--spacing);
  margin-bottom: 0.5rem;
}

.heading-1 {
  color: var(--prm-pink-1);
  margin-top: 0;
  margin-bottom: 0;
  font-size: 48px;
  letter-spacing: 0.2rem;
}

.heading-2 {
  color: var(--prm-black-4);
  font-size: 38px;
  letter-spacing: 0.2rem;
  margin-bottom: 2rem;
}

.text {
  text-align: justify;
  color: var(--prm-white-2);
  letter-spacing: 0.1rem;
  line-height: 1.8rem;
}
.btn-container {
  display: flex;
  flex-direction: column;
  margin-top: 1.2rem;
  width: 100%;
}
.btn-res,
.btn-hire {
  display: flex;
  justify-content: center;
  align-items: center;
}
.res-btn {
  background: rgba(59, 173, 227, 1);
  background: linear-gradient(
    45deg,
    var(--prm-blue-1) 0%,
    var(--prm-blue-2) 25%,
    var(--prm-blue-3) 51%,
    var(--prm-pink-2) 100%
  );
}
.res-btn:hover {
  background: linear-gradient(
    45deg,
    var(--prm-blue-1) 0%,
    var(--prm-blue-3) 80%,
    var(--prm-pink-2) 100%
  );
}
.hire-btn {
  background-color: transparent;
  border: 1px solid var(--prm-blue-1);
}
.res-btn {
  border: none;
  margin-bottom: 1rem;
}

.hire-btn:hover {
  background-color: rgb(7, 134, 255, 0.3);
  box-shadow: 0 0 10px rgb(7, 134, 255);
}
.res-btn,
.hire-btn {
  border-radius: 50px;
  height: 40px;
  width: 80%;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-family: Staatliches;
  letter-spacing: var(--spacing);
  color: var(--prm-white-1);
  font-size: 14px;
  text-align: center;
  cursor: pointer;
  transition: var(--transition);
}
.text-section p {
  animation: fade 5s;
}

.text-section .btn-container {
  animation: fade 5s;
}

@keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 100%;
  }
}

@media screen and (min-width: 500px) {
  .text-section {
    text-align: justify;
    width: 80%;
    margin: 0 auto;
  }
  .hero-text-container {
    width: 100%;
    padding: 2rem;
    height: 100%;
    position: relative;
    margin-top: 3.5rem;
  }
  .btn-container {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    margin-top: 1.2rem;
    width: 100%;
  }
  .res-btn,
  .hire-btn {
    height: 45px;
    width: 160px;
  }
  .gmail-link:hover {
    color: var(--prm-pink-2);
    -webkit-filter: drop-shadow(0px 0px 4px var(--prm-pink-1));
    -moz-filter: drop-shadow(0px 0px 4px var(--prm-pink-1));
    filter: drop-shadow(0px 0px 4px var(--prm-pink-1));
  }
  .heading-4 {
    font-size: 18px;
    margin-bottom: 0.6rem;
  }

  .heading-1 {
    font-size: 58px;
    letter-spacing: 0.4rem;
  }

  .heading-2 {
    font-size: 48px;
    margin-bottom: 1rem;
  }
  .icon-fv {
    display: none;
    flex-direction: column;
    -webkit-box-align: center;
    align-items: center;
    position: absolute;
    right: 2rem;
    top: 15rem;
    position: fixed;
    animation: appear 4s;
  }
  .icon-social {
    color: var(--prm-pink-1);
    margin: 20px 0;
    font-size: 16px;
    transition: var(--transition);
  }
  .icon-social:hover {
    transform: translateY(-15px);
    -webkit-filter: drop-shadow(0px 0px 4px var(--prm-pink-1));
    -moz-filter: drop-shadow(0px 0px 4px var(--prm-pink-1));
    filter: drop-shadow(0px 0px 4px var(--prm-pink-1));
  }
  @keyframes appear {
    from {
      transform: translateY(25rem);
    }
    to {
      transform: translateY(0rem);
    }
  }
}
@media screen and (min-width: 900px) {
  .icon-fv {
    display: flex;
  }
  .fv {
    display: flex;
  }
  .hero-text-container {
    width: 80%;
    margin: 0 auto;
    margin-top: 4.5rem;
  }
  .text-section {
    text-align: justify;
  }
  .btn-container {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    margin-top: 1.2rem;
    width: 100%;
  }
  .res-btn,
  .hire-btn {
    height: 50px;
    width: 200px;
  }

  .heading-4 {
    font-size: 18px;
    margin-bottom: 0.6rem;
  }

  .heading-1 {
    font-size: 78px;
    letter-spacing: 0.7rem;
  }

  .heading-2 {
    font-size: 58px;
    margin-bottom: 1rem;
    letter-spacing: 0.7rem;
  }
  .text {
    text-align: justify;
    color: var(--prm-white-2);
    letter-spacing: 0.1rem;
    line-height: 1.8rem;
    width: 80%;
  }
  .btn-container {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    margin-top: 1.2rem;
    width: 80%;
  }
  .icon-fv {
    right: 8rem;
    top: 15rem;
  }
}
@media screen and (min-width: 900px) {
  .btn-container {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    margin-top: 1.2rem;
    margin-top: 30px;
    width: 60%;
  }
}
