@import url("https://fonts.googleapis.com/css2?family=Dongle:wght@300&family=Overpass:ital,wght@0,100;1,200&family=Staatliches&display=swap");
/**************************VARIABLES************************/
:root {
  --prm-white-1: rgb(248, 248, 248);
  --prm-white-4: rgb(245, 245, 245);
  --prm-white-3: rgba(245, 245, 245, 0.959);
  --prm-white-2: rgb(148, 146, 146);
  --prm-black-1: rgb(15, 15, 15);
  --prm-black-2: rgb(12, 10, 10);
  --prm-black-3: rgb(29, 26, 26);
  --prm-black-4: rgb(117, 115, 115);
  --prm-blue-1: rgb(7, 134, 255);
  --prm-blue-2: #5678d6;
  --prm-blue-3: #9591c2;
  --prm-blue-4: #dbbcf8;
  --prm-pink-1: #ffb8e4;
  --prm-pink-2: #f06de5;
  --transition: all 0.3s linear;
  --spacing: 0.2rem;
  --radius: 0.25rem;
  --light-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  --dark-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

/*************************************************************/
body {
  margin: 0;
  background-color: black;
  font-family: "Staatliches", cursive, "Overpass", "Dongle", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
}

span {
  color: var(--prm-pink-1);
}

a {
  text-decoration: none;
  letter-spacing: var(--spacing);
  display: inline-block;
  text-decoration-skip-ink: auto;
  color: inherit;
  position: relative;
  transition: all 0.3s linear;
}

li {
  list-style: none;
}
p {
  margin-block-end: 0;
  margin-block-start: 0;
  padding: 0;
}
body::-webkit-scrollbar {
  width: 7px;
}

body::-webkit-scrollbar-track {
  background: black;
}

body::-webkit-scrollbar-thumb {
  background: rgb(32, 32, 32);
  border-radius: 15px;
}

body::-webkit-scrollbar-thumb:hover {
  background: rgb(27, 27, 27);
  opacity: 1;
}
