.about-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 3rem;
  padding: 2rem;
}
.p-color {
  color: var(--prm-blue-1);
}
.t-white {
  color: var(--prm-white-1);
}
.svg1 {
  height: 150px;
  position: absolute;
  width: 200px;
  top: -2rem;
  left: -3rem;
  animation: svganimate 5s infinite;
}
.left-section {
  text-align: center;
}

@keyframes svganimate {
  from {
    transform: rotate3d(1, 1, 1, 180deg);
  }
  to {
    transform: rotate3d(1, 1, 1, -180deg);
  }
}

.blob {
  display: none;
}
.avatar {
  width: 300px;
}
.right-section {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (min-width: 700px) {
  .about-container {
    margin-top: 10rem;
  }

  .about-center {
    display: flex;
    width: 100%;
    justify-content: space-around;
    align-items: center;
  }

  .left-section {
    text-align: start;
  }
  .svg1 {
    height: 350px;
    width: 300px;
    top: -4rem;
    left: -3rem;
  }
  .avatar {
    width: 350px;
  }
  .blob {
    height: 500px;
    width: 500px;
    display: flex;
    position: absolute;
    right: 0rem;
    top: 0;
    opacity: 0.7;
    z-index: -1;
  }
}
@media screen and (min-width: 1200px) {
  .about-container {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 110vh;
  }

  .about-center {
    max-width: 1100px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .p-color {
    color: var(--prm-blue-1);
  }
  .number {
    color: var(--prm-white-1);
    opacity: 0.8;
  }

  .svg1 {
    height: 350px;
    position: absolute;
    width: 300px;
    top: -2%;
    left: -5%;
    animation: svganimate 5s infinite;
  }

  @keyframes svganimate {
    from {
      transform: rotate3d(1, 1, 1, 180deg);
    }
    to {
      transform: rotate3d(1, 1, 1, -180deg);
    }
  }

  .blob {
    height: 700px;
    width: 650px;
    position: absolute;
    right: 1rem;
    top: 0;
    opacity: 0.7;
    z-index: -1;
  }
  .avatar {
    width: 400px;
  }

  .t-white {
    text-align: justify;
    color: var(--prm-white-1);
  }

  .skills {
    width: 100%;
    text-align: center;
  }

  .center {
    margin: 10px 0;
    width: 100%;
    text-align: center;
  }
}
