.project-container {
  min-height: 100vh;
  padding: 2rem 0rem;
}

.project-center {
  max-width: 1100px;
  margin: 0 auto;
  text-align: center;
}
.card-container {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 2rem 0;
}

.card-container .box {
  position: relative;
  width: 320px;
  height: 400px;
  color: white;
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px 30px;
  transition: 0.5s;
}

.card-container .box:hover {
  transform: translateY(-20px);
}

.card-container .box::before {
  border-radius: 15px;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, var(--prm-pink-2), var(--prm-blue-4));
}

.card-container .box::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, var(--prm-pink-2), var(--prm-blue-4));
  filter: blur(15px);
}

.card-container .box:nth-child(odd)::before {
  background: linear-gradient(45deg, var(--prm-blue-1), var(--prm-pink-1));
}
.card-container .box:nth-child(odd)::after {
  background: linear-gradient(45deg, var(--prm-blue-1), var(--prm-pink-1));
}

.card-container .box span {
  position: absolute;
  border-radius: 15px;
  top: 6px;
  left: 6px;
  right: 6px;
  bottom: 6px;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 2;
}

.card-container .box .content {
  position: relative;
  z-index: 10;
  padding: 1rem 2rem;
  text-align: justify;
}

.project-name {
  color: var(--prm-pink-1);
  letter-spacing: var(--spacing);
  top: -3rem;
  position: absolute;
  text-align: center;
}

.project-desc {
  line-height: 1.7rem;
  color: var(--prm-white-2);
}
.project-link {
  font-size: 14px;
  color: var(--prm-white-2);
  bottom: -3rem;
}

.links-project {
  display: flex;
  position: absolute;
  width: 80%;
  justify-content: space-between;
}
